.pagination {
    display: flex;
    align-items: center;
    justify-content: center;

    .num {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width:24px;
        height:24px;
        font-size:14px;
        border-radius: 50%;
        &.active{
            color:#fff;
            background-color: #1976d2;
        }
    }
}