@import url('pagination.scss');

#header {
  padding-bottom: 30px;
  border-bottom: 1px solid #000;
  .gnb-item {
    display: inline-flex;
    padding: 0 20px;
  }
}

.tb-wrap {
  table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    th {
      padding: 16px;
      color: #fff;
      background-color: #000;
    }
    td {
      padding: 13px;
      font-size: 14px;
      color: #555;
      text-align: center;
      border-bottom: 1px solid #eee;
    }
  }
}

